@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{text-align:center;font-family:"Source Sans Pro", sans-serif;background-image:linear-gradient(390deg, #6dc0be, #0b4919);height:100vh}.main-ui{max-width:800px;margin:2rem auto}.problem{font-size:6rem;font-weight:400;margin:2rem;color:#fff}.form{margin:2rem}.input,.submit{padding:0.5rem;border:none;outline:none}.submit{margin-left:0.5rem;color:#600080;font-weight:bold;letter-spacing:0.2px;background-color:#fff;cursor:pointer}.status{font-size:2rem;margin:2rem}.notes{color:#fff;margin:2rem}#points-needed{color:#7ecc00}#mistakes-allowed{color:red}.progress{border:1px solid #c7c7c7;border-right:none;position:relative;margin:0 2rem}.progress-inner{position:absolute;top:0;bottom:0;width:100%;background-color:#7ecc00;opacity:0.6;transform:scaleX(0);transform-origin:center left;transition:transform 0.5s ease-out}.boxes{display:flex;width:100%}.box{height:40px;border-right:1px solid #c7c7c7;flex:1 1}.overlay{position:fixed;top:0;right:0;bottom:0;left:0;background-color:rgba(255,255,255,0.8);display:flex;justify-content:center;align-items:center;opacity:0;visibility:hidden;transition:all 0.33s ease-out;transform:scale(1.2)}body.overlay-is-open .overlay,.overlay--visible{opacity:1;visibility:visible;transform:scale(1)}body.overlay-is-open .main-ui,.blurred{-webkit-filter:blur(4px);filter:blur(4px)}#end-message{font-size:4rem;padding-bottom:2rem}.reset-button{padding:1rem;font-size:2rem;background-color:#600080;color:#fff;font-weight:bold;border:none;letter-spacing:0.2px;cursor:pointer;border-radius:3px;transition:0.2s background-color ease-in}.reset-button:hover,.reset-button:focus{background-color:#7d00a7;outline:none}@keyframes showError{50%{color:red;transform:scale(1.2)}100%{color:#fff;transform:scale(1)}}.animate-wrong{animation:0.45s showError}

